<template>
	<view>
		<view class="fb-item">
			<view class="fb-item-img">
				<!-- <image :src="item.post_image" mode="aspectFill" @longpress="activeAS"></image> -->
				<!-- <image :src="baseAvatar" mode="aspectFill"></image> -->
				<text class="tag">{{item.category}}</text>
			</view>
			<view class="fb-item-content">
				<view class="fb-item-title">
					<text :class="{colortip: item.color_tip}" @click="passionActive">{{item.post_title}}</text>
				</view>
				<view class="fb-item-extra">

					<view class="fb-item-source" @click="tapAuthor">
						<uni-icons class="icons" type="personidea" size="20" color="#b8b8b8"></uni-icons>
						{{item.username}}
					</view>

					<view class="fb-item-post-time">
						<uni-icons class="icons" type="time" size="14" color="#b8b8b8"></uni-icons>
						{{item.post_date}}
					</view>
					<view v-if="item.is_original" class="original">原创</view>
					<view v-if="item.is_jb" class="original" style="margin-left: 70upx;">金币</view>
				</view>
				<view class="fb-item-description">
					<text>{{item.content}}</text>
				</view>
				<view style="display: flex;flex-direction: row;">
					<view class="fb-item-look">
						已有 <text class="fb-item-look-count">{{item.read_count}}</text> 次围观
					</view>
					<view v-if="item.comment_count>0" class="fb-item-look" style="position: absolute; right: 0;">
						发现 <text class="fb-item-look-count">{{item.comment_count}}</text> 个不明物体
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		props: {
			item: {
				type: Object,
				default () {
					return {}
				}
			}
		},
		data() {
			return {
				baseAvatar: 'https://img1.doubanio.com/lpic/s28050969.jpg',
				aSheetList: [{
					text: "收集"
				}, {
					text: "删除"
				}],
				showAS: false
			};
		},
		onLoad() {
		},
		methods: {
			passionActive() {
				
				uni.navigateTo({
					url: "/pages/freebuf/detail?link="
					+ "https://www.freebuf.com" + this.item.url
					+ "&id=" + this.item.ID
					+ "&comment_count=" + this.item.comment_count
					+ "&comment_num=" + this.item.comment_num
					+ "&read_count=" + this.item.read_count
					+ "&username=" + this.item.username
				})
			},
			tapAuthor() {
				uni.navigateTo({
					url: "/pages/world/world?connectTheWorld=" + "https://www.freebuf.com/author/" + this.item.username
				})
			},
			activeAS() {
				setTimeout(() => {
					this.showAS = true
				}, 1000);
			},
			colOrdel(index) {
				console.log(index)
				if (index === 0) {
					this.showTips(this.item.post_title, "success")
				} else {
					this.showTips(this.item.post_title, "warning")
				}
			},
			showTips(title, type) {
				this.$refs.uTips.show({
					duration: 4000,
					title: title,
					type: type
				});
			}
		},

	};
</script>

<style lang="scss">
	.fb-item {
		display: flex;
		padding: 20upx;
		margin: 20upx;
		// border-radius: 10upx;
		// box-shadow: 0 0 10upx 2upx rgba(0, 0, 0, 0.1);
		border-bottom: solid 2upx #f0f0f0;
		box-sizing: border-box;
		flex-direction: column;

		.fb-item-img {
			flex-shrink: 0;
			width: 678upx;
			height: 452upx;
			overflow: hidden;
			
			image {
				width: 100%;
				height: 100%;
			}

			.tag {
				position: absolute;
				left: 50upx;
				margin-top: 10upx;
				line-height: 1;
				padding: 10upx 20upx;
				color: #fff;
				font-size: 24upx;
				border-radius: 4upx;
				background-color: #19a358;
			}
		}

		.fb-item-content {
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			width: 100%;
			padding-left: 0;

			.fb-item-title {
				font-size: 32upx;
				color: #333;
				line-height: 48upx;
				padding-right: 60upx;
				margin: 10px 0;
				font-weight: bold;

				.colortip {
					color: #f02424;
				}

				text {
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
				}
			}

			.fb-item-description {
				display: flex;
				padding-right: 60upx;
				margin-top: 20upx;
				align-items: center;
				font-weight: 400;
				line-height: 1.6;
				font-size: 24upx;
				color: #666666;

				text {
					overflow: hidden;
					text-overflow: ellipsis;
					display: -webkit-box;
					color: #999;
					-webkit-line-clamp: 2;
					-webkit-box-orient: vertical;
				}
			}
			
			.fb-item-look {
				display: flex;
				align-items: center;
				font-size: 24upx;
				color: #999999;
				margin-top: 10upx;
			}
			
			.fb-item-look-count {
				display: flex;
				align-items: center;
				font-size: 24upx;
				color: #19a358;
				padding: 0 10upx;
			}

			.fb-item-extra {
				display: flex;
				flex-direction: row;
				// justify-content: start;
				align-items: center;
				margin-top: 0upx;

				.fb-item-source {
					display: contents;
					padding: 0 10upx;
					margin-right: 2upx;
					// border-radius: 30upx;
					align-self: center;
					// border: 1px solid $theme-color;
					font-size: 26upx;
					color: #19a358;
				}

				.fb-item-post-time {
					position: absolute;
					right: 0;
					margin-right: 2upx;
					line-height: 1.5;
					align-self: center;
					color: #b8b8b8;
					font-size: 26upx;
				}

				.icons {
					margin-right: 12upx;
				}
				
				.original {
					display: flex;
					height: 36upx;
					align-items: center;
					border-radius: 8upx;
					padding: 0 10upx;
					margin-left: 10upx;
					color: #FFFFFF;
					background-color: rgb(250, 140, 22);
					white-space: nowrap;
					border-color: transparent;
					position: absolute;
					right: 0upx;
					top: 27upx;
					align-self: center;
				}
			}
		}
	}
</style>
